.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0 8%;
  background-image: url("../../images/bkgfrontpage.svg");
  background-size: 110%;
  background-position: top;
  background-repeat: no-repeat;
}
body main {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
body main #CreateAccountHeader {
  color: #000000;
  font-size: 20px;
  font-family: "Raleway";
  font-weight: bold;
}
body main #CreateAccount {
  color: #00904A;
  text-decoration: none;
  font-size: 25px;
  font-family: "Asap";
  font-weight: bold;
  margin-bottom: 85px;
}
body main #UserPic {
  width: 26.56%;
  margin: 40px 0 18px 0;
}
body main #Logo {
  width: 39%;
  margin: 47px 0 32px 0;
}
body main #FrontPageHeader {
  color: #005140;
  font-size: 29px;
  font-family: "Asap";
  font-weight: bold;
}
body main .ButtonModal {
  background-color: #313131;
  justify-self: start;
  padding: 11px 0;
  width: 36%;
  font-size: 20px;
  font-family: "Asap";
  font-weight: normal;
  margin-bottom: 70px;
}
body main form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-column: 1/-1;
  justify-items: center;
}
body main form input {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 21px;
  padding: 6px 0;
  border: solid 2px #005140;
  border-radius: 0.4em;
  text-indent: 5px;
}
body main form label {
  justify-self: start;
  margin-bottom: 1px;
  font-size: 20px;
  font-family: "Raleway";
}
body main form .WideButton {
  width: 100%;
  padding: 11px 0;
  box-shadow: rgba(0, 0, 0, 0.16) 0 3px 6px;
  margin-bottom: 18px;
  font-size: 20px;
  font-family: "Asap";
  font-weight: normal;
}